/**
 * 
 * :
 * request('',{key:value},'(GET)')
 * .then(res=>{console.log(res)})
 */
import { getToken, removeToken } from "./auth";
async function request(mehtod, params, type, headers, callBack) {
  //request
  if (!type) {
    type = 'GET';
  }
  let header = {
    "Accept": "application/json",
    "Content-Type": "application/json; charset=UTF-8"
  };
  
  let http = {
    url: mehtod,
    data: params,
    method: type,
    header: Object.assign(header, headers)
  };
  let promise = new Promise((resolve, reject) => {
    uni.request(http).then(res => {
		console.log(res)
      resolve(res[1].data);
    }).catch(err => {
      reject(err);
      console.log(err);
    });
  });
  return promise;
}

async function requestJson(mehtod, params, type, headerParams, callBack) {
  //request
  let header = {
    //
    'X-Requested-With': 'XMLHttpRequest',
    "Accept": "application/json",
    "Content-Type": "application/json; charset=UTF-8"
  };
  if(headerParams){
	  header = Object.assign(header, headerParams);
  }
  let http = {
    url:  mehtod,
    data: params,
    method: type,
    header: header
  };
  let promise = new Promise((resolve, reject) => {
    uni.request(http).then(res => {
      resolve(res);
    }).catch(err => {
      reject(err);
      console.log(err);
    });
  });
  return promise;
}

async function requestForm(mehtod, params, type, callBack) {
  //request
  let header = {
    //
    'X-Requested-With': 'XMLHttpRequest',
    "Accept": "application/json",
    "Content-Type": "application/x-www-form-urlencoded"
  };
  let http = {
    url:  mehtod,
    data: params,
    method: type,
    header: header
  };
  let promise = new Promise((resolve, reject) => {
    uni.request(http).then(res => {
      resolve(res);
    }).catch(err => {
      reject(err);
      console.log(err);
    });
  });
  return promise;
}
export default {
  request,
  requestJson,
  requestForm
};